.header-color {
    background: #6788f3;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #d3557b, #6788f3);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #d3557b, #6788f3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
    background: #6788f3;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #d3557b, #6788f3);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #d3557b, #6788f3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.avatar-img {
    height: 200px;
    border-radius: 100px;
    margin-top: 5em;
}

.banner-text {
    background-color: #f2dbe5;
    opacity: .8;
    width: 60%;
    margin: auto;
    border-radius: 10px;
}

.banner-text h1 {
    font-size: 50px;
    font-weight: bold;
    color: #80639d;
    padding-top: 15px;
    margin-bottom: 10px;
}

.banner-text h2 {
    font-size: 40px;
    font-weight: bold;
    color: #80639d;
    margin-top: 10px;
}

.banner-text hr {
    border-top: 5px dotted #80639d;
    width: 50%;
    margin: auto;
}

.banner-text p {
    color: #80639d;
    font-size: 20px;
    padding: 1em;
}

.social-links {
    display: flex;
    justify-content: center;
    width: 50%;
    margin: auto;
}

.social-links i {
    color: #80639d;
    font-size: 3em;
    padding: 0 10px 15px;
}

.footer {
    background: #6788f3;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #d3557b, #6788f3);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #d3557b, #6788f3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 40px;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
}

.footer p {
    margin: 0;
}

@media screen and (max-width: 768px) {
    .landing-grid {
        position: relative;
    }
    .banner-text {
        width: 90%;
    }
    .mdl-cell {
        margin: 0!important;
    }
    .avatar-img {
        margin-top: 2em;
    }
}